
// (float) offset: proportion of the item's height

export function isInView(input, offset = 0) {
	const element = input;
	const top = (input.top) ? input.top : element.getBoundingClientRect().top;
	const bottom = (input.bottom) ? input.bottom : top + element.offsetHeight;
	const offsetHeight = element.offsetHeight * offset;
	const wHeight = window.innerHeight;
	const ypos = document.documentElement.scrollTop;

	if (ypos > bottom - offsetHeight) return false;
	if (ypos + wHeight < top + offsetHeight) return false;
	return true;
}
