import { query, queryOne } from '@artcommacode/q';
import { u as µ } from 'umbrellajs';

const Embeds = {

	embeds: [],

	init() {
		const embeds = this.embeds;
		query('.embed-placeholder').map((el) => {
			const embed = {};
			embed.element = µ(el);
			embed.container = µ(el.parentNode);
			embed.src = embed.element.attr('data-src');
			embeds.push(embed);
		});
		this.bindPlaceholders();
	},

	bindPlaceholders() {
		for (let i = 0; i < this.embeds.length; i++) {
			const embed = this.embeds[i];
			embed.element.on('click', () => this.swapEmbed(embed));
		}
	},

	swapEmbed(embed) {
		embed.element.off('click');

		let src = embed.src;
		src += (src.charAt(src.length) === '?') ? '' : '?';
		src += (src.match('&autoplay=1')) ? '' : '&autoplay=1';
		src += (src.match('&controls=0')) ? '' : '&controls=0';
		src += (src.match('&rel=0')) ? '' : '&rel=0';

		const iframe = µ('<iframe>')
			.attr({
				src,
				frameborder: 0,
				class: 'swapped-embed',
				width: 650,
			});
		embed.element.addClass('loading');
		setTimeout(() => {
			embed.element.addClass('loaded');
		}, 900);
		embed.container.append(iframe);
	},
};

window.addEventListener('load', Embeds.init());
