// import $ from 'jquery';
import * as h from './Helpers.js';
import { query, queryOne } from '@artcommacode/q';

const Fade = {

	elements: [],

	init() {
		query('.scrollfade').map((el, i) => {
			const fader = {};
			fader.index = i;
			fader.element = el;
			fader.visible = false;
			fader.delay = Math.floor(Math.random() * 300) + 50;
			this.elements.push(fader);
		});
	},

	watch() {
		function fadeWatch() {
			const visibleCount = Fade.elements.filter(element => element.visible);
			if (visibleCount.length === Fade.elements.length) window.addEventListener('scroll', fadeWatch);
			for (const fader of Fade.elements) {
				if (h.isInView(fader.element, 0.25)) {
					setTimeout(() => { fader.element.classList.add('fade-in'); }, fader.delay);
					fader.visible = true;
				}
			}
		}
		fadeWatch();
		window.addEventListener('scroll', fadeWatch);
	},
};

document.addEventListener('DOMContentLoaded', Fade.init());
window.onload = Fade.watch();
